<template>
  <el-form>
    <div style="margin-top: 150px">
      <ve-line :data="chartData" :settings="chartSettings"></ve-line>
    </div>
  </el-form>
</template>
<script>
import VeLine from 'v-charts/lib/line'
import request from "@/utils/request";

export default {
  name: 'Curve',
  components: {VeLine},
  data() {
    return {
      chartData: {
        columns: ['deviceId', 'powerTest'],
        rows: [
          {
            'powerTest': 2,
            // 'countTest': 11,
            // '2': 2,
            // '3': 0.5,
            // '4': 0.63,
            // '5': 0.8,
            // '6': 2,
            // '7': 2.1,
            // '8': 1.7
          },
          // {
          //   '': '105',
          //   '1': 3.8,
          //   '2': 6.2,
          //   '3': 6.4,
          //   '4': 4.5,
          //   '5': 4.5,
          //   '6': 6.5,
          //   '7': 7.1,
          //   '8': 5.5
          // },
        ]
      },
      chartSettings: {
        yAxisName: ['']
      },
    }
  },
  mounted() {
    this.load();
  },
  methods: {
    load() {
      request.get("/device/searchAllPowerTest", {}).then(res => {
        const device = res.data.slice(-10);
        //  map 
        this.rows = device.map(obj => {
          const { openTest: oldValue, ...rest } = obj; //  openTest 
          return { "openTest": oldValue, ...rest }; //  "openTest"
        });
        console.log(this.rows)
      })
    },
  },
}
</script>
