import axios from 'axios'
import { Message } from 'element-ui'
import NProgress from 'nprogress'//
import 'nprogress/nprogress.css'
NProgress.configure({
    easing: 'ease',
    speed: 500,
    showSpinner: false
})
const baseURL = 'http://122.51.67.189:10000'
// const baseURL = 'http://localhost:10000'
// const baseURL = 'http://43.137.10.141:10000'
// const baseURL = 'https://www.yuetongroad.com/aed'
// axios
const request = axios.create({
    // []VUEprocess.envURL HOST.env.production baseURL url  baseURL  url 
    baseURL: baseURL,
    // 
    timeout: 100000,
    responseType: 'json',
    // 
    headers: {
        'Content-Type': 'application/json;charset=UTF-8',
        isToken:false
    }
})
// 
request.interceptors.request.use(
    (config) => {
        NProgress.start() // 
        if (config.method === 'post') {
            config.data = config.data || {}
        }
        // let userInfo = localStorage.getItem('satoken')
        // let satoken = userInfo ? userInfo : ''
        // if (satoken) {
        //     config.headers.satoken = satoken
        //     config.headers.Authorization = satoken
        // }
        // 
        if (config.type && config.type === 'file') {
            config.timeout = 120000
        }
        return config
    },
    (error) => {
        Message.error('')
        // promisereject
        return Promise.reject(error)
    },
)
// 
request.interceptors.response.use(
    (response) => {
        NProgress.done()
        const res = response.data
        if (res) {
            if (res.state === false) {
                return Promise.reject(res)
            } else {
                return res
            }
        } else {
            return Promise.reject(res)
        }
    },
    (error) => {
        if (error.message !== '') {
            Message.error(error.message || '')
            if (window.location.hash !== '#/login') {
                window.location.href = '/#/login'
            }
        }
        return Promise.reject(error)
    },
)

export default request
