<template>
  <div class="baidumap" id="allmap">
  </div>
</template>
<script>
import request from "@/utils/request"

export default {
  name: 'Map',
  data() {
    return {
      positions: [],
      show1: true,
      show2: true
    }
  },
  mounted() {
    this.load();
  },
  methods: {
    load() {
      request.get("/device", {}).then(res => {
        this.positions = res.data.slice(0, 10);
        console.log(this.positions);
        this.baiduMap();
      })
    },
    baiduMap() {
      var Map = new BMap.Map('allmap'); // 
      Map.enableScrollWheelZoom(true); //
      Map.addControl(new BMap.NavigationControl());
      Map.addControl(new BMap.ScaleControl());
      Map.addControl(new BMap.OverviewMapControl());
      Map.addControl(new BMap.MapTypeControl());
      // map.setMapStyle({ style: 'midnight' }) //


      // map.clearOverlays();
      //
      //
      this.positions.forEach(position => {
        // 
        var point = new BMap.Point(position.lon, position.lat);

        // 
        var marker = new BMap.Marker(point);

        // 
        Map.addOverlay(marker);

        var openTestText = (position.openTest === 1) ? '' : '';
        // 
        var sContent =
            '<div style="width:200px;"><p style="font-size:16px;font-weight:bold;margin-top: 10px;color:#D07852">' +
            position.deviceId +
            '</p><p style="font-size:13px;margin: 5px 0;">' +
            position.lon + ',' + position.lat +
            '</p><p style="font-size:13px;margin: 5px 0;">' +
            position.countTest +
            '</p><p style="font-size:13px;margin: 5px 0;">' +
            position.powerTest +
            '</p><p style="font-size:13px;margin: 5px 0;">' +
            openTestText +
            '</p><p style="font-size:13px;margin: 5px 0;">' +
            position.lux +
            '</p></div>';

        // 
        var infoWindow = new BMap.InfoWindow(sContent);

        // 
        marker.addEventListener('click', function () {
          this.openInfoWindow(infoWindow);
        });
        // 
        Map.centerAndZoom(point, 15);
        // //
        var infoWindow = new BMap.InfoWindow(sContent);
        marker.openInfoWindow(infoWindow);

        marker.addEventListener('click', function () {
          this.openInfoWindow(infoWindow);
        })
      });


    }
  }
}
</script>
<style scoped>
.baidumap {

  width: 1000px;
  height: 600px;
  border: 10px solid rgb(0, 0, 0);
  margin-left: 40px;
  border-radius: 15px;

}

/*   logo */
/deep/ .baidumap > .BMap_cpyCtrl {
  display: none !important;
}

/deep/ .baidumap > .anchorBL {
  display: none !important;
}

</style>
