<template>
  <div>
    <el-form class="manager-form">
      <el-form-item label="">
        <el-input
            style="width: 200px;margin-left: 10px;float: left;" v-model="managerName"
            placeholder=""></el-input>
        <el-row :gutter="24" type="flex" align="middle" style="margin-left: 15px">
          <el-col :span="15">
            <el-button type="primary" @click="search_device()" class="authority_btn">
            </el-button>
            <el-button type="primary" @click="handleAdd()"></el-button>
            <el-button type="primary" @click="del()">
              
            </el-button>
            <el-button type="primary" @click="reset()"></el-button>
          </el-col>
        </el-row>
      </el-form-item>
      <el-table
          :data="tableData"
          style="width: 100%"
          :show-overflow-tooltip="true"
          @selection-change="handleSelectionChange"
          :header-cell-style="{'text-align':'center'}"
          :cell-style="{'text-align':'center'}">
        <el-table-column type="selection" width="50"/>
        <el-table-column
            prop="deviceId"
            label="ID">
        </el-table-column>
        <el-table-column
            prop="identityGet"
            label="">
          <template slot-scope="scope">
            <template v-if="scope.row.identityGet === 'Weixin'">
              
            </template>
            <template v-else>
              {{ scope.row.identityGet }}
            </template>
          </template>
        </el-table-column>
        <el-table-column
            prop="managerName"
            label="">
        </el-table-column>
        <el-table-column
            prop="picture"
            label="">
          <template slot-scope="scope">
            <img class="open_img" :src="'http://43.137.10.141:8088/image/' +scope.row.picture" alt="logo" @click="upload(scope.row.picture)">
          </template>
        </el-table-column>
        <el-table-column
            prop="usetime"
            label="">
        </el-table-column>
        <el-table-column label="" min-width="150" align="center">
          <template slot-scope="scope">
            <el-button type="text" @click="viewItem(scope.row)"></el-button>
            <el-button @click="handleEdit(scope.row)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination @size-change="sizeChange" @current-change="currentChange"
                     :current-page="page" :page-size="size" :page-sizes="pageSizes"
                     layout="total, sizes, prev, pager, next, jumper" :total="total" style="float: left;">
      </el-pagination>
    </el-form>
    <AddOrModify v-if="dialogFormVisible" :dialogFormVisible.sync="dialogFormVisible" :manager-id="id" :type="type"
                 @refresh="load"/>
  </div>
</template>
<script>
import request from "@/utils/request";
import AddOrModify from "@/views/manage/manager/AddOrModify";
import UserService from "@/api/userService";
import DeviceService from "@/api/deviceService";
export default {
  name: 'UserOpen',
  components: {AddOrModify},
  data() {
    return {
      tableData: [],
      managerName: "",
      page: 1, //
      size: 10, //
      total: 0, //
      pageSizes: [10, 20, 30, 50, 100], //
      pageNum: 1,
      pageSize: 5,
      dialogFormVisible: false,
      type: '',
      id: '',
      multipleSelection: []
    }
  },
  mounted() {
    this.load();
  },
  methods: {
    load() {
      DeviceService.searchList().then(res => {
        this.tableData = Array.from(res.data).filter(item => item.identityGet && item.identityGet.length > 0 && item.identityGet !== "Admin").splice(
            (this.page - 1) * this.size,
            this.size
        );
        this.total = this.tableData.length;
      })
    },
    del() {
      let ids = this.multipleSelection.map((item) => item.id).join(",");
      let self = this;
      self
          .$confirm(", ?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning",
          })
          .then(() => {
            UserService.delete(ids)
                .then((res) => {
                  if (res.code === 200) {
                    self.$message.success(res.message);
                  } else {
                    self.$message.warning(res.message);
                  }
                }).finally(() => {
              self.load();
            });
          });

    },
    reset() {
      this.managerName = '';
      this.load();
    },
    upload(pic) {
      window.open(this.baseUrl + pic, '_blank')
    },
    search_device() {
      UserService.findByName(this.managerName).then(res => {
        this.tableData = Array.from(res.data).splice(
            (this.page - 1) * this.size,
            this.size
        )
        this.total = res.data.length;
      })
    },
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    handleEdit(row) {
      this.type = 'edit';
      this.id = row.id;
      this.dialogFormVisible = true
    },
    viewItem(row) {
      this.type = 'detail';
      this.id = row.id;
      this.dialogFormVisible = true
    },
    handleAdd() {
      this.type = 'add';
      this.id = undefined;
      this.dialogFormVisible = true;
    },
    //page
    currentChange(val) {
      console.log("", val);
      this.page = val;
      this.load();
    },
    //sizesize
    sizeChange(val) {
      console.log("", val);
      this.size = val;
      this.page = 1;
      this.load();
    },
  }
}
</script>
<style scoped>
@import "../../../assets/css/manager";
</style>